.about{
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
    display: flex;
    padding-right: 5%;
    padding-top: 2rem;
}

.about .left{
    padding: 1rem;
    text-align: center;
    margin: auto;
    max-width: 600px;
}

.about .btn{
    display: inline-block;
    padding: 0.5rem 2rem;
}

.about .left p{
    margin: 1.2rem 0;
    font-size: 1.2rem;
}

.about .right{
    max-width: 700px;
}

.right .img-container{
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    align-items: center;
    text-align: center;
}   

.right .img{
    max-width: 90%;
    border: 1px solid #333;
}

.right .top{
    grid-column: 1 /span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}

.right .bottom{
    grid-column: 4 / -1;
    grid-row: 1;
    z-index: 5;
}
